/* ======================================================================== */
/* 2. button */
/* ======================================================================== */
.button {
  position: relative;
  display: inline-block;
  font-size: 13px;
  line-height: 1;
  font-weight: 500;
  white-space: nowrap;
  cursor: pointer;
  box-shadow: none;
  border-width: 2px;
  border-style: solid;
  padding: 18px 42px;
  border-radius: 96px;
  color: var(--color-dark-4);
  transition: all 0.2s ease-in-out;
  transition-property: color, background-color, border-color;
}

.button:focus {
  outline: none;
}

.button:hover .button__label-normal .button__title {
  transform: translate(0%, -100%);
  opacity: 0;
}

.button:hover .button__label-normal .button__icon {
  opacity: 0;
  transform: translate(100%, 0%);
}

.button:hover .button__label-hover .button__title {
  transform: translate(0%, 0%);
  opacity: 1;
}

.button:hover .button__label-hover .button__icon {
  opacity: 1;
  transform: translate(0%, 0%);
}

.button_icon {
  display: inline-flex;
  border: none;
}

.button_icon .button__label {
  padding: 0;
}

.button__label-hover {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button__label-hover .button__title {
  transform: translate(0%, 100%);
  opacity: 0;
}

.button__label-hover .button__icon {
  transform: translate(-100%, 0%);
  opacity: 0;
}

.button__label-normal .button__title {
  transform: translate(0%, 0%);
  opacity: 1;
}

.button__title {
  display: inline-block;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}

.button__label {
  display: block;
  padding: 18px 42px;
}

.button__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
  font-size: 75%;
}

.button__icon_before {
  margin-right: 0.5em;
}

.button__icon_after {
  margin-left: 0.25em;
}

.button_fullwidth {
  width: 100%;
}

.button-fullwidth .button {
  width: 100%;
}

@media screen and (max-width: 1280px) {
  .button {
    padding: 16px 32px;
  }
  .button__label {
    padding: 16px 32px;
  }
}

@media screen and (max-width: 991px) {
  .button {
    padding: 16px 30px;
  }
  .button__label {
    padding: 16px 30px;
  }
  .button__icon i {
    font-size: 20px;
  }
}

