/* ======================================================================== */
/* 5. buttonCircle */
/* ======================================================================== */
.button-circle {
  position: relative;
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  width: calc(1 * (60 * 1px));
  height: calc(1 * (60 * 1px));
  padding: 0 !important;
  opacity: .85;
  transition-property: opacity, color, background-color, border-color;
}

@media screen and (min-width: 320px) {
  .button-circle {
    width: calc(1 * (60 * 1px + (90 - 60) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle {
    width: calc(1 * (90 * 1px));
  }
}

@media screen and (min-width: 320px) {
  .button-circle {
    height: calc(1 * (60 * 1px + (90 - 60) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle {
    height: calc(1 * (90 * 1px));
  }
}

.button-circle:hover {
  opacity: 1;
}

.cursorfollower .button-circle[data-arts-cursor]:hover {
  border-color: transparent !important;
}

.button-circle_medium {
  width: calc(1 * (90 * 1px));
  height: calc(1 * (90 * 1px));
}

@media screen and (min-width: 320px) {
  .button-circle_medium {
    width: calc(1 * (90 * 1px + (140 - 90) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle_medium {
    width: calc(1 * (140 * 1px));
  }
}

@media screen and (min-width: 320px) {
  .button-circle_medium {
    height: calc(1 * (90 * 1px + (140 - 90) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle_medium {
    height: calc(1 * (140 * 1px));
  }
}

.button-circle_big {
  width: calc(1 * (130 * 1px));
  height: calc(1 * (130 * 1px));
}

@media screen and (min-width: 320px) {
  .button-circle_big {
    width: calc(1 * (130 * 1px + (160 - 130) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle_big {
    width: calc(1 * (160 * 1px));
  }
}

@media screen and (min-width: 320px) {
  .button-circle_big {
    height: calc(1 * (130 * 1px + (160 - 130) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle_big {
    height: calc(1 * (160 * 1px));
  }
}

.button-circle__inner {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.button-circle__geometry {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid var(--color-border-dark);
  border-radius: 50%;
}

.button-circle__arrow {
  width: 30px;
  font-size: calc(12 * 1px);
}

@media screen and (min-width: 320px) {
  .button-circle__arrow {
    font-size: calc(12 * 1px + (18 - 12) * ((100vw - 320px) / 1600));
  }
}

@media screen and (min-width: 1920px) {
  .button-circle__arrow {
    font-size: calc(18 * 1px);
  }
}

.button-circle__arrow:before {
  width: 30px;
  margin: 0;
}

.button-circle__label {
  padding: 0 !important;
}

/* ======================================================================== */
/* 6. card */
/* ======================================================================== */
.card {
  width: 100%;
  height: 100%;
  position: relative;
}

.card__content ul, .card__content ol {
  padding-left: 0 !important;
}

.card__counter {
  position: absolute;
  right: 40px;
  bottom: 30px;
}

.card__arrow {
  margin-left: 1rem;
}

@media screen and (max-width: 991px) {
  .card__counter {
    position: absolute;
    right: 25px;
    bottom: 15px;
  }
}

/* ======================================================================== */
/* 7. buttonCircles */
/* ======================================================================== */
.button-circles {
  display: inline-flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 30px;
  height: 30px;
  padding: 0 !important;
}

.button-circles.block-counter {
  margin-left: 4px;
}

.button-circles .block-counter__counter {
  top: -12px;
  left: calc(100% + 6px);
}

.button-circles__circle {
  position: relative;
  width: 12px;
  height: 12px;
  outline: none;
  display: inline-block;
  border: 2px solid var(--color-border-dark);
  vertical-align: middle;
  border-radius: 100%;
}

.button-circles__circle:nth-child(1) {
  align-self: flex-start;
}

.button-circles__circle:nth-child(2) {
  align-self: flex-start;
}

.button-circles__circle:nth-child(3) {
  align-self: flex-end;
}

.button-circles__circle:nth-child(4) {
  align-self: flex-end;
}

.button-circles__circle svg {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  stroke-width: 12px;
  z-index: 50;
}

.button-circles__circle svg .circle {
  stroke: #fff;
}

@media screen and (max-width: 767px) {
  .button-circles {
    width: 30px;
    height: 30px;
  }
  .button-circles__circle {
    width: 12px;
    height: 12px;
  }
  .button-circles__circle svg {
    width: 12px;
    height: 12px;
  }
}

/* ======================================================================== */
/* 8. cardPortfolio */
/* ======================================================================== */
.card-portfolio {
  overflow: hidden;
  border-radius: 4px;
}

.card-portfolio__content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.card-portfolio__content_small {
  padding: 60px;
}

.card-portfolio__wrapper-img img {
  transform: scale(1.03);
}

/* ======================================================================== */
/* 9. categories */
/* ======================================================================== */
.categories {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 13px;
  font-weight: 500;
}

.categories li {
