  cursor: progress !important;
}

.cursor-none {
  cursor: none !important;
}

.cursor-none * {
  cursor: none !important;
}

.is-dragging {
  cursor: none !important;
}

.is-dragging * {
  cursor: none !important;
}

.is-dragging .is-dragging__blocker {
  visibility: visible;
  opacity: 1;
  z-index: 100;
}

.is-dragging__blocker {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition-delay: 0.2s;
}

