/* ======================================================================== */
/* 33. listBackgrounds */
/* ======================================================================== */
.list-backgrounds_striped .list-backgrounds__item:nth-of-type(odd) {
  background-color: var(--color-light-2);
}

.list-backgrounds_striped .list-backgrounds__item:nth-of-type(even) {
  background-color: var(--color-light-1);
}

.list-backgrounds__item {
  position: relative;
  overflow: hidden;
}

.list-backgrounds__wrapper-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.list-backgrounds__wrapper-img > * {
  height: 100%;
}

.list-backgrounds__wrapper-img img {
  transform: scale(1.03);
}

.list-backgrounds__overlay {
  z-index: 1 !important;
}

.list-backgrounds__works-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.list-backgrounds__works-list li {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  transition: all 0.2s ease-in-out;
}

.list-backgrounds__works-list li:first-child {
  margin-top: 0;
}

.list-backgrounds__works-list li:last-child {
  margin-bottom: 0;
}

.list-backgrounds__overlay {
  transition: all 0.2s ease-in-out;
}

.list-backgrounds__item_hover .list-backgrounds__overlay {
  opacity: 0.6;
}

/* ======================================================================== */
/* 34. listBackgroundsThemes */
/* ======================================================================== */
[data-arts-theme-text='light'] .list-backgrounds_striped .list-backgrounds__item:nth-of-type(even) {
  background-color: #191919;
}

[data-arts-theme-text='light'] .list-backgrounds_striped .list-backgrounds__item:nth-of-type(odd) {
  background-color: #202020;
}

/* ======================================================================== */
/* 35. listDots */
/* ======================================================================== */
.list-dots {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.list-dots li {
  display: inline-block;
  vertical-align: middle;
  margin-top: 0.5em;
}

.list-dots li:not(:last-child):after {
  content: '';
  display: inline-block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: var(--color-gray-2);
  vertical-align: middle;
  margin-left: 2.5em;
  margin-right: 2.5em;
  transition: transform 0.2s ease-in-out;
}

@media screen and (max-width: 991px) {
  .list-dots li {
    display: block;
  }
  .list-dots li:first-child {
    margin-top: 0;
  }
  .list-dots li:not(:last-child):after {
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
  }
}

/* ======================================================================== */
/* 36. logo */
/* ======================================================================== */
.logo {
  position: relative;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
}

.logo__wrapper-img {
  position: relative;
  align-items: center;
  flex: 1 0 auto;
  height: 100%;
  margin-right: 15px;
}

.logo__wrapper-img img {
  transition: all 0.2s ease-in-out;
  display: block;
}

.logo__text-title {
  display: block;
  font-size: 20px;
  font-weight: bold;
  line-height: 1.3;
  color: var(--paragraph-color-dark);
}

.logo__text-tagline {
  display: block;
  font-style: italic;
  font-size: 12px;
  font-weight: bold;
  line-height: 1.3;
}

.logo__img-secondary {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.logo__description {
  margin-top: 1em;
}

/* ======================================================================== */
/* 37. modal */
/* ======================================================================== */
.modal-open {
  overflow: hidden;
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 6000;
