.pagination {
  border-top: 2px solid var(--color-border-dark);
  padding: 10px 0 0;
  font-weight: 500;
}

.pagination .nav-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pagination .nav-links__container {
  margin-left: auto;
  margin-right: auto;
}

.pagination .page-numbers {
  display: inline-block;
  line-height: 1;
  padding: 12px 20px;
  background-image: none;
  vertical-align: middle;
  transition-property: color;
}

.pagination .page-numbers.prev {
  font-size: 24px;
  font-weight: normal;
  margin-left: -20px;
}

.pagination .page-numbers.next {
  font-size: 24px;
  font-weight: normal;
  margin-right: -20px;
}

.pagination .page-numbers:not(a) {
  color: var(--color-dark-4);
}

.pagination .page-numbers.current {
  color: var(--color-dark-4);
}

.page-links {
  border-top: 2px solid var(--color-border-dark);
  padding: 10px 0 0;
}

.page-links .page-number {
  display: inline-block;
  line-height: 1;
  font-weight: 600;
  padding: 0 15px;
  border: none;
}

.page-links .post-page-numbers:not(a) {
  color: var(--color-dark-4);
}

.comments-pagination {
  text-align: center;
}

.comments-pagination .page-numbers {
  display: inline-block;
  line-height: 1;
  font-weight: 600;
  padding: 0 10px;
  transition-property: color;
}

@media screen and (max-width: 991px) {
  .pagination {
    padding: 8px 0 0;
  }
  .page-links {
    padding: 8px 0 0;
  }
}

/* ======================================================================== */
/* 73. postMeta */
/* ======================================================================== */
.post-meta {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 13px;
  line-height: 1;
  font-weight: 500;
}

.post-meta i {
  font-size: 14px !important;
  vertical-align: middle;
  margin-right: 4px;
}

.post-meta li {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  line-height: 1.6;
  vertical-align: middle;
}

.post-meta li span {
  vertical-align: middle;
}

.post-meta li:not(:last-child):after {
  content: '/';
  color: var(--color-dark-1);
  display: inline-block;
  margin-left: 6px;
  margin-right: 5px;
  transition: all 0.2s ease-in-out;
}

.post-meta ul {
  padding-left: 0;
}

.post-meta a {
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
}

.post-meta_block li {
  display: block;
  margin: 0.5em 0;
}

.post-meta_block li:not(:last-child):after {
  display: none;
}

.post-meta_flex {
  display: flex;
  justify-content: space-between;
}

.post-meta_flex li:not(:last-child):after {
  display: none;
}

.post-meta__divider {
  display: inline-block;
  vertical-align: middle;
  margin: 0 1em;
  width: calc(1 * (60 * 1px));
  height: 1px;
  background-color: var(--color-border-dark);
}

@media screen and (min-width: 320px) {
  .post-meta__divider {
    width: calc(1 * (60 * 1px + (100 - 60) * ((100vw - 320px) / 1600)));
  }
}

@media screen and (min-width: 1920px) {
  .post-meta__divider {
    width: calc(1 * (100 * 1px));
  }
}

.post-meta_mini li:after {
  display: none !important;
}

.post-meta_mini-small li:after {
  display: none !important;
}

.post-meta_mini-small .post-meta__divider {
  width: 50px;
}

/* ======================================================================== */
/* 74. sidebar */
/* ======================================================================== */
/* ======================================================================== */
/* 75. tags */
/* ======================================================================== */
.tagcloud a, .widget .tagcloud a {
  display: inline-block;
  font-size: 13px;
  font-weight: 500;
  line-height: 1;
  background-color: transparent;
  padding: 8px 14px;
  margin-bottom: 6px;
  margin-right: 4px;
  border-radius: 100px;
  background-color: var(--color-light-3);
}

.tagcloud a:hover, .widget .tagcloud a:hover {
  border-color: var(--color-dark-1);
}

.tagcloud ul, .widget .tagcloud ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.tagcloud ul li, .widget .tagcloud ul li {
  display: inline-block;
  margin-bottom: 0;
}

/* ======================================================================== */
/* 76. select */
/* ======================================================================== */
select {
  display: block;
  border: 1px solid var(--color-border-dark);
  border-radius: 2px;
  padding: 8px 35px 8px 15px;
  background-color: transparent;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url("../img/general/select_black.png");
  background-position: right 15px bottom 50%;
  background-repeat: no-repeat;
  background-size: 6px 4px;
}

/* ======================================================================== */
/* 77. inputSearch */
/* ======================================================================== */
.input-search__input {
  padding-right: 30px;
}

.input-search__submit {
  display: inline-block;
  font-size: 18px;
  width: 24px;
  height: 24px;
  padding: 0;
  position: absolute;
  right: 0;
  top: 12px;
  bottom: 0;
  margin: auto;
  background: transparent;
  border: none;
  color: var(--color-gray-3);
  outline: none;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

.input-search__submit:hover {
  color: var(--color-dark-1);
}

.input-search__submit:focus {
  outline: none;
}

/* ======================================================================== */
/* 78. formContacts */
/* ======================================================================== */
@media screen and (max-width: 991px) {
  .form-contact {
    padding-left: var(--bs-gutter-x);
    padding-right: var(--bs-gutter-x);
  }
}

/* ======================================================================== */
/* 79. inputFloat */
/* ======================================================================== */
.input-float {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 0;
}

.input-float__label {
  position: absolute;
  top: 0.75em;
  left: 0;
  margin: auto;
  display: block;
  font-size: 16px;
  transition: all 0.2s ease-in-out;
  transform-origin: left center;
  cursor: text;
  color: var(--color-gray-3);
}

.input-float__input {
  display: block;
  width: 100%;
  width: 100%;
  border-bottom: 1px solid var(--color-border-dark);
  border-top: none;
  border-right: none;
  border-left: none;
  outline: none;
  padding: 10px 0 4px;
  transition: all 0.2s ease-in-out;
  background-color: transparent;
  border-radius: 0;
  color: inherit;
}

.input-float__input_textarea {
  resize: none;
  height: 200px;
}

.input-float__input_focused + .input-float__label, .input-float__input_not-empty + .input-float__label {
  transform: scale(0.8571) translateY(-1.75rem);
}

.input-float__input_focused {
  border-color: var(--color-dark-1);
}

.input-float__input_focused + .input-float__label {
  color: var(--color-dark-1);
}

/* ======================================================================== */
/* 80. lazyLoad */
/* ======================================================================== */
.lazy-wrapper {
  display: inline-block;
  width: 100%;
  vertical-align: middle;
}

/* ======================================================================== */
/* 81. footer */
/* ======================================================================== */
.footer {
  color: var(--color-gray-3);
  z-index: 100;
}

.footer .widgettitle {
  display: block;
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
  margin-top: 0;
  margin-bottom: 1em;
}

.footer .widgettitle:after {
  display: none;
}

.footer__divider {
  display: block;
  position: absolute;
  top: 0;
  left: calc(var(--bs-gutter-x) / 2);
  right: calc(var(--bs-gutter-x) / 2);
  height: 1px;
  padding-left: 0;
  padding-right: 0;
  width: auto;
  background-color: var(--color-border-dark);
}

/* ======================================================================== */
/* 82. footerLogo */
/* ======================================================================== */
[data-arts-footer-logo="primary"] .logo__img-primary {
  opacity: 1;
  visibility: visible;
}

[data-arts-footer-logo="primary"] .logo__img-secondary {
  opacity: 0;
  visibility: hidden;
}

[data-arts-footer-logo="secondary"] .logo__img-primary {
  opacity: 0;
  visibility: hidden;
}

[data-arts-footer-logo="secondary"] .logo__img-secondary {
  opacity: 1;
  visibility: visible;
}

/* ======================================================================== */
/* 83. preloader */
/* ======================================================================== */
.preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 400;
  overflow: hidden;
}

.preloader_header-hidden + #page-header {
  transition: opacity 0.3s ease;
}

.preloader_header-hidden:not(.preloader_ended) + #page-header {
  opacity: 0;
  visibility: hidden;
  transform: translateY(50px);
}

.preloader_header-menu-hidden + #page-header .menu, .preloader_header-menu-hidden + #page-header .header__burger {
  transition: opacity 0.3s ease;
}

.preloader_header-menu-hidden:not(.preloader_ended) + #page-header .menu, .preloader_header-menu-hidden:not(.preloader_ended) + #page-header .header__burger {
  opacity: 0;
  visibility: hidden;
}

.preloader:not(.preloader_ended) + #page-header {
  pointer-events: none !important;
}

.preloader:not(.preloader_ended) + #page-header * {
  pointer-events: none !important;
}

.preloader__wrapper {
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  top: var(--gutter-vertical, 120px);
  left: var(--gutter-horizontal, 120px);
  right: var(--gutter-horizontal, 120px);
  bottom: calc(var(--fix-bar-vh, 30px) + var(--gutter-vertical, 100px));
}

.preloader__counter {
  font-family: var(--font-primary);
  font-weight: 100;
  line-height: 1;
  font-size: calc(96 * 1px);
  letter-spacing: -10px;
  white-space: nowrap;
}

@media screen and (min-width: 320px) {
  .preloader__counter {
    font-size: calc(96 * 1px + (212 - 96) * ((100vw - 320px) / 1600));
  }
}

@media screen and (min-width: 1920px) {
  .preloader__counter {
    font-size: calc(212 * 1px);
  }
}

.underline.preloader__counter_started {
  animation-name: loading;
  animation-duration: 20s;
  transition: background-size 1.2s ease;
}

.underline.preloader__counter_paused {
  animation-play-state: paused;
}

.underline.preloader__counter_ended {
  animation-duration: 1s;
}

@keyframes loading {
  0% {
    background-size: 0% 2px;
  }
  100% {
    background-size: 100% 2px;
  }
}

/* ======================================================================== */
/* 84. header */
/* ======================================================================== */
.header {
  pointer-events: none;
  padding-top: calc(var(--gutter-vertical) / 1.5);
}

.header a, .header select, .header input {
  pointer-events: initial;
}

.header:not(.opened) .header__label-burger_closed-hover, .header:not(.opened) .header__label-burger_opened-open, .header:not(.opened) .header__label-burger_opened-hover {
  transform: translateY(100%);
  opacity: 0;
}

.header:not(.opened) .header__burger:hover .header__label-burger_closed-open {
  transform: translateY(-100%);
  opacity: 0;
}

.header:not(.opened) .header__burger:hover .header__label-burger_closed-hover {
  transform: translateY(0%);
  opacity: 1;
}

.header.opened .header__label-burger_closed-open, .header.opened .header__label-burger_closed-hover, .header.opened .header__label-burger_opened-hover {
  transform: translateY(100%);
  opacity: 0;
}

.header.opened .header__burger:hover .header__label-burger_opened-open {
  transform: translateY(-100%);
  opacity: 0;
}

.header.opened .header__burger:hover .header__label-burger_opened-hover {
  transform: translateY(0%);
  opacity: 1;
}

.cursorfollower .header__overlay-menu-back[data-arts-cursor] .header__label {
  pointer-events: none !important;
  transition: all 0.3s ease !important;
}

.cursorfollower .header__overlay-menu-back[data-arts-cursor]:hover .header__label_status {
  transform: translate(25px, -100%);
  opacity: 0;
}

.cursorfollower .header__overlay-menu-back[data-arts-cursor]:hover .header__label_opened-back {
  transform: translate(25px, -50%);
  opacity: 1;
}

.cursorfollower .header__burger[data-arts-cursor] .header__burger-label {
  pointer-events: none !important;
}

.cursorfollower .header__burger[data-arts-cursor]:hover .header__burger-label {
  transform: translate(-30px, -50%);
}

.header__controls {
  pointer-events: none;
}

.header_absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;
}

.header_fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;
}

.header__container {
  padding-left: 0;
  padding-right: 0;
  position: relative;
  z-index: 501;
}

.header_menu-hidden .header__burger {
  opacity: 0;
  visibility: hidden;
}

.header__burger {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 50px;
  height: 50px;
  vertical-align: middle;
  cursor: pointer;
  pointer-events: initial;
  z-index: 500;
  margin-right: -12px;
}

.header__burger:hover .header__burger-line:before {
  transform: translateX(100%);
}

.header__burger:hover .header__burger-line:after {
  transform: translateX(0%);
}

.header__burger-label {
  display: inline-block;
  height: 14px;
  position: absolute;
  top: 50%;
  right: 100%;
  transform: translate(0, -50%);
  text-align: right;
  transition: all 0.3s ease !important;
}

.header__label-burger_inner {
  position: absolute;
  top: 0;
  right: 0;
  transition: transform 0.3s ease 0s, opacity 0.3s ease 0s;
}

.header__burger-line {
  position: relative;
  width: 26px;
  height: 2px;
  margin-top: 2px;
  margin-bottom: 2px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  transition: all 0.6s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.header__burger-line:nth-of-type(1) {
  transition-delay: 0ms;
}

.header__burger-line:nth-of-type(2):before, .header__burger-line:nth-of-type(2):after {
  transition-delay: 50ms;
}

.header__burger-line:nth-of-type(3):before, .header__burger-line:nth-of-type(3):after {
  transition-delay: 100ms;
}

> .header__burger-line:first-of-type {
  margin-top: auto;
  margin-bottom: 2px;
}

> .header__burger-line:last-of-type {
  margin-top: 2px;
  margin-bottom: auto;
}

.header__burger-line:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0%);
  background-color: var(--color-dark-2);
  transition: all 0.3s ease;
}

.header__burger-line:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(calc(-100% - 4px));
  background-color: var(--color-dark-2);
  transition: all 0.5s ease;
}

.header__burger_opened .header__burger-line:nth-of-type(1), .header__burger_opened:hover .header__burger-line:nth-of-type(1) {
  transform: scaleX(1) rotate(45deg) translate(2px, 2px);
}

.header__burger_opened .header__burger-line:nth-of-type(2), .header__burger_opened:hover .header__burger-line:nth-of-type(2) {
  transform: scaleX(1) rotate(-45deg) translate(2px, -2px);
}

.header__burger_opened .header__burger-line:nth-of-type(3), .header__burger_opened:hover .header__burger-line:nth-of-type(3) {
  transform-origin: right center;
  transform: scaleX(0);
}

.header__wrapper-overlay-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 500;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
}

.header__label {
  white-space: nowrap;
  transition: color 0.3s ease;
  font-size: 14px;
  line-height: 0.9;
  font-weight: 500;
}

.header__label_status {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: calc(100% + 2px);
  transition: all 0.3s ease;
}

.header__label_opened-back {
  position: absolute;
  top: 50%;
  transform: translate(0%, 100%);
  left: calc(100% + 2px);
  opacity: 0;
  transition: all 0.3s ease;
}

.header__label_side {
  position: absolute;
  top: 50%;
  left: 0;
  padding-left: 0;
  padding-right: 0;
  transform: translateY(-50%);
}

.header__wrapper-overlay-widgets .widget {
  margin-bottom: 0;
}

.header__wrapper-menu {
  position: relative;
  margin-top: auto;
  margin-bottom: auto;
  padding: calc(var(--gutter-vertical) * 1.5) var(--gutter-horizontal);
  width: 100%;
  height: 100%;
  display: flex !important;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.header__wrapper-menu .scroll-content {
  height: 100%;
  width: 100%;
  display: flex !important;
  flex-direction: column;
}

.header__wrapper-slider {
  padding-left: var(--gutter-horizontal);
  padding-right: var(--gutter-horizontal);
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  pointer-events: auto !important;
}

.header__overlay-menu-back {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  width: 50px;
  height: 50px;
  left: calc(41.666667% + var(--gutter-horizontal) - 20px);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: initial;
  z-index: 50;
  transition: color 0.3s ease;
}

.header__overlay-menu-back i {
  font-size: 32px !important;
}

.header__overlay-menu-back:hover .header__label_status {
  transform: translate(0%, -100%);
  opacity: 0;
}

.header__overlay-menu-back:hover .header__label_opened-back {
  transform: translate(0%, -50%);
  opacity: 1;
}

.header__overlay-menu-info {
  opacity: 0;
  visibility: hidden;
}

.header_menu-right .menu .sub-menu {
  left: auto;
  right: 1em;
}

.header_menu-right .menu .sub-menu ul {
  left: auto;
  right: calc(100% + 1px);
}

.header_menu-split-center .header__col-right .menu .sub-menu {
  left: auto;
  right: 1em;
}

.header_menu-split-center .header__col-right .menu .sub-menu ul {
  left: auto;
  right: calc(100% + 1px);
}

.header__menu-gradient {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 60;
}

.header__menu-gradient_top {
  top: 0;
  height: calc(var(--gutter-vertical) * 2);
}

.header__menu-gradient_bottom {
  bottom: 0;
  height: calc(var(--gutter-vertical));
}

.header__scroll-container {
  width: 100%;
  height: 100%;
  display: flex !important;
}

.header__scroll-container .scroll-content {
  width: 100%;
  margin-top: auto;
  margin-bottom: auto;
}

@media screen and (max-width: 991px) {
  .header__burger {
    width: 32px;
    height: 32px;
    margin-right: -3px;
    margin-left: 3px;
  }
  .header__burger-label {
    margin-right: 6px;
  }
  .header__wrapper-menu {
    margin-top: 0;
    padding-top: 5em;
    padding-bottom: 5em;
  }
  .header__overlay-menu-info {
    display: none;
  }
  .header__overlay-menu-back {
    left: calc(var(--gutter-horizontal) * -1);
  }
  .header__wrapper-slider {
    padding-top: 1.5em;
    padding-bottom: 5em;
  }
  .header__menu-gradient_top {
    height: calc(var(--gutter-vertical) * 3);
  }
  .header__menu-gradient_bottom {
    height: calc(var(--gutter-vertical) * 3);
  }
}

/* ======================================================================== */
/* 85. headerLogo */
/* ======================================================================== */
[data-arts-header-logo="primary"] .logo__img-primary, .preloader[data-arts-preloader-logo="primary"]:not(.preloader_ended) + #page-header .logo__img-primary {
  opacity: 1;
  visibility: visible;
}

[data-arts-header-logo="primary"] .logo__img-secondary, .preloader[data-arts-preloader-logo="primary"]:not(.preloader_ended) + #page-header .logo__img-secondary {
  opacity: 0;
  visibility: hidden;
}

[data-arts-header-logo="primary"] .logo__text-title, .preloader[data-arts-preloader-logo="primary"]:not(.preloader_ended) + #page-header .logo__text-title {
  color: var(--paragraph-color-dark) !important;
}

[data-arts-header-logo="primary"] .logo__text-tagline, .preloader[data-arts-preloader-logo="primary"]:not(.preloader_ended) + #page-header .logo__text-tagline {
  color: var(--color-gray-2) !important;
}

[data-arts-header-logo="secondary"] .logo__img-primary, .preloader[data-arts-preloader-logo="secondary"]:not(.preloader_ended) + #page-header .logo__img-primary {
  opacity: 0;
  visibility: hidden;
}

[data-arts-header-logo="secondary"] .logo__img-secondary, .preloader[data-arts-preloader-logo="secondary"]:not(.preloader_ended) + #page-header .logo__img-secondary {
  opacity: 1;
  visibility: visible;
}

[data-arts-header-logo="secondary"] .logo__text-title, .preloader[data-arts-preloader-logo="secondary"]:not(.preloader_ended) + #page-header .logo__text-title {
  color: #fff !important;
}

[data-arts-header-logo="secondary"] .logo__text-tagline, .preloader[data-arts-preloader-logo="secondary"]:not(.preloader_ended) + #page-header .logo__text-tagline {
  color: var(--color-gray-2) !important;
}

/* ======================================================================== */
/* 86. headerSticky */
/* ======================================================================== */
.js-header-sticky {
  transition-property: padding-top, padding-bottom, box-shadow, background-color;
  transition-duration: 0.4s;
  transition-timing-function: ease;
  box-shadow: 0px 0px 30px 0px rgba(24, 24, 24, 0);
