  width: 136px;
  flex: 1 0 auto;
}

.slider-projects-fullscreen__thumbs {
  flex: 1 1 100%;
}

.slider-projects-fullscreen__button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 0;
  padding: 0;
}

.slider-projects-fullscreen__col-content {
  flex: 1 1 36vw;
  width: 36vw;
  max-width: 720px;
}

.slider-projects-fullscreen__col-images {
  flex: 2 1 64vw;
  height: 100%;
  width: 64vw;
  max-width: 64vw;
}

.slider-projects-fullscreen__slide {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-projects-fullscreen__slide img {
