  }
}

@media screen and (max-width: 1280px) {
  .slider-projects-fullscreen__slide img {
    width: 100%;
    height: auto;
    opacity: .2;
  }
}

@media screen and (max-width: 991px) {
  .slider-projects-fullscreen__wrapper-scrollbar_mobile-absolute {
    position: absolute;
    display: inline-block;
    right: 0;
