  }
  .slider-projects-fullscreen__col-images {
    flex: 1 1 100%;
    width: 100%;
    max-width: unset;
    margin-left: calc(var(--gutter-horizontal) * -1);
    margin-right: calc(var(--gutter-horizontal) * -1);
    overflow: hidden;
  }
  .slider-projects-fullscreen__sidebar {
    width: 0;
  }
  .slider-projects-fullscreen__col-content {
