/* ======================================================================== */
.background-hover {
	position: relative;
	display: block;
	&:hover {
		*, *:after, *:before {
			color: #fff !important; }
		a {
			opacity: 0.9;
			&:hover {
				opacity: 1; } }
		.background-hover__image {
			opacity: 1; }
		.background-hover__overlay {
			opacity: 1; } }
	.background-hover__overlay {
		opacity: 0;
		@include trans1; } }
.background-hover__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: .01;
	@include trans1; }
