.text-start {
  .section__headline {
    margin-left: 0;
    margin-right: auto; } }
.text-center {
  .section__headline {
    margin-left: auto;
    margin-right: auto; } }
.text-end {
  .section__headline {
    margin-left: auto;
    margin-right: 0; } }
.section__bg {
