						margin-top: -2em; } }
			tr:last-child {
				td:last-child {
					border-bottom: none;
					margin-bottom: -2em; } } } } }

/* ======================================================================== */
/* 51. themes */
/* ======================================================================== */
[data-arts-theme-text="light"] {
	color: $color-gray-2;
