	.slider__scrollbar-handle {
		background-color: #fff;
		color: #fff; }
	.slider__counter_current {
		color: #fff; }
	.slider__arrow {
		&:hover {
			color: #fff; } }
	.slider__dot svg .circle {
		stroke: #fff; }
