/* ======================================================================== */
/* 80. lazyLoad */
/* ======================================================================== */
.lazy-wrapper {
  display: inline-block;
  width: 100%;
  vertical-align: middle; }

/* ======================================================================== */
/* 81. footer */
/* ======================================================================== */
.footer {
