/* ======================================================================== */
/* 82. footerLogo */
/* ======================================================================== */
[data-arts-footer-logo="primary"] {
	.logo__img-primary {
		opacity: 1;
		visibility: visible; }
	.logo__img-secondary {
		opacity: 0;
		visibility: hidden; } }
[data-arts-footer-logo="secondary"] {
	.logo__img-primary {
		opacity: 0;
		visibility: hidden; }
	.logo__img-secondary {
		opacity: 1;
		visibility: visible; } }

/* ======================================================================== */
/* 83. preloader */
/* ======================================================================== */
.preloader {
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 400;
	overflow: hidden; }
.preloader_header-hidden {
	+ #page-header {
		transition: opacity 0.3s ease; }
	&:not(.preloader_ended) {
		+ #page-header {
			opacity: 0;
			visibility: hidden;
			transform: translateY(50px); } } }
.preloader_header-menu-hidden {
	+ #page-header {
		.menu, .header__burger {
			transition: opacity 0.3s ease; } }
	&:not(.preloader_ended) {
		+ #page-header {
			.menu, .header__burger {
				opacity: 0;
				visibility: hidden; } } } }
.preloader:not(.preloader_ended) + #page-header {
	pointer-events: none !important;
	* {
		pointer-events: none !important; } }
.preloader__wrapper {
	display: flex;
	flex-wrap: wrap;
	position: fixed;
	top: var(--gutter-vertical, 120px);
	left: var(--gutter-horizontal, 120px);
	right: var(--gutter-horizontal, 120px);
	bottom: calc(var(--fix-bar-vh, 30px) + var(--gutter-vertical, 100px)); }
.preloader__counter {
	font-family: var(--font-primary);
	font-weight: 100;
	line-height: 1;
	@include fluid-type(96, 212);
	letter-spacing: -10px;
	// opacity: 0
	white-space: nowrap; }
.underline {
	&.preloader__counter_started {
		animation-name: loading;
		animation-duration: 20s;
		transition: background-size 1.2s ease; }
	&.preloader__counter_paused {
		animation-play-state: paused; }
	&.preloader__counter_ended {
		animation-duration: 1s; } }

@keyframes loading {
	0% {
		background-size: 0% 2px; }
	100% {
		background-size: 100% 2px; } }

/* ======================================================================== */
/* 84. header */
/* ======================================================================== */
.header {
	pointer-events: none;
	padding-top: calc(var(--gutter-vertical) / 1.5);
	a, select, input {
		pointer-events: initial; }
	&:not(.opened) {
		.header__label-burger_closed-hover, .header__label-burger_opened-open, .header__label-burger_opened-hover {
			transform: translateY(100%);
			opacity: 0; }
		.header__burger {
			&:hover {
				.header__label-burger_closed-open {
					transform: translateY(-100%);
					opacity: 0; }
				.header__label-burger_closed-hover {
					transform: translateY(0%);
					opacity: 1; } } } }
	&.opened {
		.header__label-burger_closed-open, .header__label-burger_closed-hover, .header__label-burger_opened-hover {
			transform: translateY(100%);
			opacity: 0; }
		.header__burger {
			&:hover {
				.header__label-burger_opened-open {
					transform: translateY(-100%);
					opacity: 0; }
				.header__label-burger_opened-hover {
					transform: translateY(0%);
					opacity: 1; } } } } }
.cursorfollower {
	.header__overlay-menu-back {
		&[data-arts-cursor] {
			.header__label {
				pointer-events: none !important;
				transition: all 0.3s ease !important; }
			&:hover {
				.header__label_status {
					transform: translate(25px, -100%);
					opacity: 0; }
				.header__label_opened-back {
					transform: translate(25px, -50%);
					opacity: 1; } } } }
	.header__burger {
		&[data-arts-cursor] {
			.header__burger-label {
				pointer-events: none !important; }
			&:hover {
				.header__burger-label {
					transform: translate(-30px, -50%); } } } } }
.header__controls {
	pointer-events: none; }
.header_absolute {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 500; }
.header_fixed {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 500; }
.header__container {
	padding-left: 0;
	padding-right: 0;
	position: relative;
	z-index: 501; }
.header_menu-hidden {
	.header__burger {
		opacity: 0;
		visibility: hidden; } }
.header__burger {
	display: inline-flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 50px;
	height: 50px;
	vertical-align: middle;
	cursor: pointer;
	pointer-events: initial;
	z-index: 500;
	margin-right: -12px;
	&:hover {
		.header__burger-line {
			&:before {
				transform: translateX(100%); }
			&:after {
				transform: translateX(0%); } } } }
.header__burger-label {
	display: inline-block;
	height: 14px;
	position: absolute;
	top: 50%;
	right: 100%;
	transform: translate(0, -50%);
	text-align: right;
	transition: all 0.3s ease !important;
	// &:after
 }	// 	content: attr(data-arts-burger-label-close)
.header__label-burger_inner {
	position: absolute;
	top: 0;
	right: 0;
	transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, }

.header__burger-line {
	// position: absolute
	position: relative;
	width: 26px;
	height: 2px;
	// left: 0
	// right: 0
	margin-top: 2px;
	margin-bottom: 2px;
	margin-left: auto;
	margin-right: auto;
	overflow: hidden;
	// background-color: var(--color-dark-2)
	// background-color: #fff
	@include trans2;
	&:nth-of-type(1) {
		&:before, &:after {}
		transition-delay: 0ms; }
	&:nth-of-type(2) {
		&:before, &:after {
			transition-delay: 50ms; } }
	&:nth-of-type(3) {
		&:before, &:after {
			transition-delay: 100ms; } }
	> &:first-of-type {
		margin-top: auto;
		margin-bottom: 2px; }
	> &:last-of-type {
		margin-top: 2px;
		margin-bottom: auto; }
	&:before {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		transform: translateX(0%);
		background-color: var(--color-dark-2);
		transition: all 0.3s ease; }
	&:after {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		transform: translateX(calc(-100% - 4px));
		background-color: var(--color-dark-2);
		transition: all 0.5s ease; } }
.header__burger_opened, .header__burger_opened:hover {
	.header__burger-line {
		&:nth-of-type(1) {
			transform: scaleX(1) rotate(45deg) translate(2px, 2px); }
		&:nth-of-type(2) {
			transform: scaleX(1) rotate(-45deg) translate(2px, -2px); }
		&:nth-of-type(3) {
			transform-origin: right center;
			transform: scaleX(0); } } }
.header__wrapper-overlay-menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 500;
	overflow: hidden;
	opacity: 0;
	visibility: hidden; }
.header__label {
	white-space: nowrap;
	transition: color 0.3s ease;
	font-size: 14px;
	line-height: 0.9;
	font-weight: 500; }
.header__label_status {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: calc(100% + 2px);
	transition: all 0.3s ease; }
.header__label_opened-back {
	position: absolute;
	top: 50%;
	transform: translate(0%, 100%);
	left: calc(100% + 2px);
	opacity: 0;
	transition: all 0.3s ease; }
.header__label_side {
