	top: 50%;
	left: 0;
	padding-left: 0;
	padding-right: 0;
	transform: translateY(-50%); }
.header__wrapper-overlay-widgets {
	.widget {
		margin-bottom: 0; } }
.header__wrapper-menu {
	position: relative;
	margin-top: auto;
	margin-bottom: auto;
	padding: calc(var(--gutter-vertical) * 1.5) var(--gutter-horizontal);
	width: 100%;
	height: 100%;
	display: flex !important;
	flex-direction: column;
	overflow-x: hidden;
	overflow-y: auto;
