	align-items: center;
	justify-content: center;
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto 0;
	width: 50px;
	height: 50px;
	left: calc(41.666667% + var(--gutter-horizontal) - 20px);
	cursor: pointer;
	opacity: 0;
	visibility: hidden;
	pointer-events: initial;
	z-index: 50;
	transition: color 0.3s ease;
	i {
		font-size: 32px !important; }
	&:hover {
		.header__label_status {
			transform: translate(0%, -100%);
			opacity: 0; }
		.header__label_opened-back {
			transform: translate(0%, -50%);
			opacity: 1; } } }
.header__overlay-menu-info {
	opacity: 0;
	visibility: hidden; }
.header_menu-right {
	.menu {
		.sub-menu {
			left: auto;
			right: 1em;
			ul {
				left: auto;
				right: calc(100% + 1px); } } } }
.header_menu-split-center {
	.header__col-right {
		.menu {
			.sub-menu {
				left: auto;
				right: 1em;
				ul {
					left: auto;
					right: calc(100% + 1px); } } } } }

.header__menu-gradient {
	display: block;
	position: absolute;
	left: 0;
	right: 0;
	z-index: 60; }
.header__menu-gradient_top {
	top: 0;
	height: calc(var(--gutter-vertical) * 2); }
.header__menu-gradient_bottom {
	bottom: 0;
	height: calc(var(--gutter-vertical)); }
.header__scroll-container {
	width: 100%;
	height: 100%;
	display: flex !important;
	.scroll-content {
		width: 100%;
		margin-top: auto;
		margin-bottom: auto; } }

@media screen and (max-width: $md) {
	.header__burger {
		width: 32px;
		height: 32px;
		margin-right: -3px;
		margin-left: 3px; }
	.header__burger-label {
		margin-right: 6px; }
	.header__wrapper-menu {
